h6, h5, h4, h3, h2, h1, p {
  margin: 0;
  text-align: center;
}

div, section, aside, nav, header, footer, main {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

.containerForSubPage {
  padding: 100px 120px 40px 120px;
  display: flex;
  flex-grow: 10;
  position: relative;
}
.containerForSubPage.withImage {
  padding-top: 0;
}

.flexibleBlock {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100vh;
}

